.slider-section .swiper-slide {
  min-height: 270px;
  color: #fff;
  justify-content: center;
  align-items: center;
  font-size: 32px;
  transition: all .5s ease-in-out;
  display: flex;
  width: 270px !important;
}

.slider-section .swiper-slide.swiper-slide-next {
  width: 162px !important;
}

.swiper-btns {
  justify-content: end;
  gap: 20px;
  margin-top: 32px;
  display: flex;
}

.swiper-button-prev, .swiper-button-next {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: relative;
}

.swiper-button-prev.hero-slider {
  background-color: rgba(0, 0, 0, .65);
  position: absolute;
  top: 50%;
  left: 5%;
}

.swiper-button-next.hero-slider {
  background-color: rgba(0, 0, 0, .65);
  position: absolute;
  top: 50%;
  right: 5%;
}

.swiper-button-next:after, .swiper-rtl .swiper-button-prev:after {
  content: "";
  background-color: none;
}

.swiper-button-prev:after, .swiper-rtl .swiper-button-next:after {
  content: "";
  position: absolute;
  top: 80%;
}

